<template>
  <div class="home">
    <div id="container"></div>
  </div>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import Stats from "three/examples/jsm/libs/stats.module.js";

let scene; // 场景
let renderer; // 渲染器
let camera; // 相机
let controls; // 相机控制器
let npcMixer; // npc 动作混合器
const clock = new THREE.Clock(); // 计时工具
let npc; // npc
let city;
let run; //奔跑动画
let delta = 1800;
let AnimationId = null;

let runningAction;
let standAction;
let danceAction;
let walkingAction;

export default {
  name: "HatchNaruto",
  data() {
    return {
      keypress: { w: false, a: false, s: false, d: false },
      animations: [],
      animationMap: [],
      npcStatus: "",
      clipAction: null,
      actionAction: null,
      actionPlay: "",
      request_render: null,
      AnimationId: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.createSence();
      this.loadModel();
      this.initEvents();
      this.creatGlobal();
    });
  },
  watch: {
    actionPlay(newValue, oldValue) {
      this.play(newValue);
    },
  },
  methods: {
    createSence() {
      //定义画布canvas的尺寸
      const width = window.innerWidth;
      const height = window.innerHeight;

      //创建一个场景
      scene = new THREE.Scene();

      //创建一个渲染器
      renderer = new THREE.WebGLRenderer({
        antialias: true,
      });

      //创建辅助坐标系
      const axesHelper = new THREE.AxesHelper(200);

      // scene.add(axesHelper);

      // 创建网格辅助器
      const grid = new THREE.GridHelper(2000, 50, 0xffffff, 0xffffff);
      grid.material.opacity = 0.2;
      grid.material.transparent = true;
      // scene.add(grid);

      //设置渲染尺寸
      renderer.setSize(width, height);

      //创建一个相机
      camera = new THREE.PerspectiveCamera(30, width / height, 1, 8000);

      //相机的位置
      camera.position.set(0, 40, 50);

      //创建一个点光源
      const pointLight = new THREE.PointLight(0xffffff, 1.0);

      pointLight.position.set(120, 140, 240);

      // scene.add(pointLight);

      //创建环境光源
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.4);
      scene.add(ambientLight);

      //可视化点光源
      const pointLightHelper = new THREE.PointLightHelper(pointLight, 10);

      scene.add(pointLightHelper);

      //创建一个轨道控制器
      controls = new OrbitControls(camera, renderer.domElement);

      controls.maxPolarAngle = Math.PI * 0.49;
      controls.minPolarAngle = Math.PI * 0.3;

      //进行控制监听操作
      controls.addEventListener("change", () => {
        renderer.render(scene, camera);
        // console.log(camera.position, "camera.position");
      });

      window.onresize = function () {
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.render(scene, camera);
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
      };

      //获取元素dom
      const container = document.getElementById("container");
      console.log(renderer.domElement, "container");
      container.appendChild(renderer.domElement);

      //创建stats对象
      const stats = new Stats();
      //stats.domElement:web页面上输出计算结果,一个div元素，
      document.body.appendChild(stats.domElement);
      // 渲染函数
      function render() {
        stats.update();
        renderer.render(scene, camera); //执行渲染操作
        requestAnimationFrame(render); //请求再次执行渲染函数render，渲染下一帧
      }
      render();
      renderer.render(scene, camera);

      console.log(window.devicePixelRatio, "2121331");
    },
    loadModel(callback) {
      // 创建gltf加载器
      const loader = new GLTFLoader();

      // 加载NPC模型
      loader.load("models/naruto/hatch_naruto.glb", async (gltf) => {
        npc = gltf.scene;
        npc.position.set(0, 0.5, 0);
        this.animations = gltf.animations;
        // npc.rotation.order = "YXZ";
        npc.rotation.y = Math.PI;

        npcMixer = new THREE.AnimationMixer(gltf.scene);
        this.initAnimation();

        const scale = 5;
        npc.scale.set(scale, scale, scale);
        scene.add(npc);
        camera.lookAt(npc.position);
        console.log(gltf, "npc");
      });
      loader.load("models/city/ccity_building_set_1.glb", async (gltf) => {
        gltf.scene.position.set(400, 0, 400);
        city = gltf.scene;
        //创建一个 THREE.Box3() 对象，使用 setFromObject() 方法将其设置为模型的边界盒
        const box = new THREE.Box3().setFromObject(city);
        const x = box.max.x - box.min.x;
        const y = box.max.y - box.min.y;
        const z = box.max.z - box.min.z;
        const maxDim = Math.max(x, y, z);
        const scale = 3800 / maxDim;
        city.scale.set(scale / 2, scale / 2, scale);
        scene.add(city);
      });
    },
    play(animation) {
      // let AnimationId = null;
      if (AnimationId !== null) {
        cancelAnimationFrame(AnimationId);
      }
      if (animation == "Running") {
        runningAction.fadeIn(0.2).play();
        standAction.fadeOut(0.2).stop();
        danceAction.fadeOut(0.2).stop();
      } else if (animation == "Hip Hop Dancing") {
        danceAction.fadeIn(0.2).play();
        runningAction.stop();
        standAction.fadeOut(0.2).stop();
        // setTimeout(() => {
        //   danceAction.fadeOut(0.2).stop();
        // }, 2000);
      } else {
        runningAction.stop();
        standAction.fadeIn(0.3).play();
      }
      const clock = new THREE.Clock();
      function loop() {
        AnimationId = window.requestAnimationFrame(loop);
        let delta = clock.getDelta();
        // 更新播放器相关的时间
        npcMixer.update(delta);
      }
      loop();
    },
    initEvents() {
      document.addEventListener("keydown", (evt) => {
        console.log(evt, "evt");
        const key = evt.key.toLocaleLowerCase();
        if (Reflect.ownKeys(this.keypress).includes(key)) {
          this.keypress[key] = true;
          this.actionPlay = "Running";
          this.update();
        }
      });
      document.addEventListener("keyup", (evt) => {
        const key = evt.key.toLocaleLowerCase();
        if (Reflect.ownKeys(this.keypress).includes(key)) {
          this.keypress[key] = false;
          this.actionPlay = "mixamo.com";
        }
      });
      document.addEventListener("mousedown", (evt) => {
        if (evt.button == 2) {
          this.actionPlay = "Hip Hop Dancing";
        }
        console.log(evt.button);
      });
    },

    handleRotation() {
      const angleYFromCamera = Math.atan2(
        camera.position.x - npc.position.x,
        camera.position.z - npc.position.z
      );

      const offsetRadian = this.getOffsetRadian();
      const quaternion = new THREE.Quaternion();
      quaternion.setFromAxisAngle(
        new THREE.Vector3(0, 1, 0),
        offsetRadian + angleYFromCamera
      );
      npc.quaternion.rotateTowards(quaternion, 0.2); // 第二个参数为旋转最大值，可理解为过渡效果
    },

    getOffsetRadian() {
      // let radian = npc.rotation.y; // 获取当前旋转的弧度值
      let radian;
      if (this.keypress["w"]) {
        radian = Math.PI; // 前方的旋转的弧度
        if (this.keypress["a"]) {
          radian = Math.PI * 1.25; // 右前方的旋转弧度
        } else if (this.keypress["d"]) {
          radian = Math.PI * 0.75; // 左前方的旋转弧度
        }
      } else if (this.keypress["s"]) {
        radian = 0; // 后方的旋转弧度
        if (this.keypress["a"]) {
          radian = Math.PI * -0.25; // 左后方的旋转弧度
        } else if (this.keypress["d"]) {
          radian = Math.PI * 0.25; // 右后方的旋转弧度
        }
      } else if (this.keypress["a"]) {
        radian = -Math.PI * 0.5; // 左边的旋转弧度
      } else if (this.keypress["d"]) {
        radian = Math.PI * 0.5; // 右边的旋转弧度
      }
      return radian;
    },

    update() {
      // 更新播放器相关的时间
      this.handleRotation();
      this.handleMove();
    },
    handleMove() {
      // 获取运动速率，这里乘以一个0.001是避免速率太大导致运动幅度太大
      const speed = 0.001;
      // (this.status === Status.WALK ? this.walkSpeed : this.runSpeed) * 0.001;
      const direction = new THREE.Vector3();
      // 获取人物的初始方向，就是摄像机照的方向
      camera.getWorldDirection(direction);
      // direction.normalize();
      // direction.y = 0;
      // 将按键对应的旋转弧度应用到初始方向里面去，因为默认设置了Math.PI的旋转弧度，所以这里要加上
      const offsetRadian = this.getOffsetRadian();
      direction.applyAxisAngle(
        new THREE.Vector3(0, 1, 0),
        offsetRadian + Math.PI
      );

      // 将方向向量乘以速率和delta来获得X和Z轴的速率
      const moveX = speed * direction.x * delta;
      const moveZ = speed * direction.z * delta;

      this.lookAtPlayer(moveX, moveZ);
    },
    lookAtPlayer(moveX, moveZ) {
      // 重新赋值位置
      npc.position.x += moveX;
      npc.position.z += moveZ;
      camera.position.x += moveX;
      camera.position.z += moveZ;
      const target = npc.position.clone();
      // target.y = npc.position.y;
      controls.target = target;
      controls.update();
    },
    initAnimation() {
      this.animationMap = new Map();
      this.animations.forEach((animation) => {
        const actionClip = npcMixer.clipAction(animation);
        this.animationMap.set(animation.name, actionClip);
      });
      runningAction = this.animationMap.get("Running");
      standAction = this.animationMap.get("mixamo.com");
      danceAction = this.animationMap.get("Hip Hop Dancing");
      console.log(this.animationMap, "animations");
    },
    creatGlobal() {
      const global = new THREE.SphereGeometry(1000, 1000, 1000);

      const loader = new RGBELoader();
      loader.load("texture/sky.hdr", (texture) => {
        texture.mapping = THREE.EquirectangularReflectionMapping;
        //将加载的材质texture设置给背景和环境
        scene.background = texture;
        scene.environment = texture;
        const material = new THREE.MeshBasicMaterial({
          map: texture,
        });
        //创建一个网格模型
        const mesh = new THREE.Mesh(global, material);
        //将网格模型放在原点
        mesh.position.set(0, 0, 0);
        mesh.geometry.scale(1, 1, -1);

        scene.add(mesh);
      });
    },
  },
};
</script>
